.Basics {
    height: auto;
    width: auto;
}

.Basics--FullName {
    justify-content: center;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    min-font-size: 5vw;
    max-font-size: 30px;
    line-height: 2vh;
    text-align: center;

    color: #FFFFFF;
}

.Basics--Title {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    min-font-size: 3vw;
    max-font-size: 20px;
    padding: 0 1vh;
    text-align: center;

    color: #F3BF99;
}


.Basics--MailButton  img {
    margin: auto 0;
    padding: 3px;
}

.Basics--MailButton p {
    padding: 0;
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    min-font-size: 3vw;
    max-font-size: 20px;
    line-height: 1vh;
    text-align: center;
}

.Basics--MailButton {
    padding: 2px;
    margin: 1vh auto;

    text-align: center;
    justify-content: center;
    align-content: center;
    display: flex;
    max-width: 300px;
    min-height: 40px;
    width: 300px;
    box-sizing: border-box;
    background: #FFFFFF;
    border: 1px solid #D1D5DB;

    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
}